
import { PropType } from 'vue';
import { ScoreOption } from '../_provider/ScoreMode/ScoreModeStrategy';

export default {
  props: {
    value: {
      default: 0,
      type: Number,
    },
    options: {
      default() {
        return [];
      },
      type: Array as PropType<ScoreOption[]>,
    },
    label: {
      default: '',
      type: String,
    },
    type: {
      default: 'star',
      type: String,
    },
  },
  emits: ['update:value'],
  data: () => ({
    modelValue: 0,
    hoverValue: null,
  }),
  computed: {
    fillNumber() {
      if (this.hoverValue) return this.hoverValue;
      return this.modelValue;
    },
  },
  watch: {
    value: {
      handler(newValue) {
        if (this.modelValue !== newValue) this.modelValue = newValue;
      },
      immediate: true,
    },
    modelValue: {
      handler(newValue) {
        this.$emit('update:value', Number(newValue));
      },
    },
  },
  methods: {
    updateValue(value) {
      if (value === this.modelValue) {
        this.modelValue = 0;
        return;
      }
      this.modelValue = value;
    },
    hover(item) {
      this.hoverValue = item;
    },
  },
};
